body {
    overflow-x: hidden;
}

.users__container {
    text-align: center;
}

.users__btn-container {
    text-align: right;
    margin: 20px 0px;
}

.users__btn {
    margin-left: 5px;
}

.custom-control-label {
    margin-left: 10px;
}

.custom-checkbox .custom-control-input:checked ~ .custom-control-label::before {
    background-color: #5bc0de !important;
    border-color: #5bc0de;
}