body {
    overflow-x: hidden;
}

.orders__container {
    text-align: center;
}

.orders__btn-container {
    text-align: right;
    margin: 20px 0px;
}

.orders__filters-container {
    text-align: left;
    margin: 20px 0px;
}

.orders__btn {
    margin-left: 5px;
}

.custom-control-label {
    margin-left: 10px;
}

.custom-checkbox .custom-control-input:checked ~ .custom-control-label::before {
    background-color: #5bc0de;
    border-color: #5bc0de;
}

.sorting-columns {
    cursor: pointer;
}