.auth__main-container {
    position: relative;
    width: 100%;
}

.auth__left-container, .auth__right-container {
    /* position: absolute; */
    height: 96vh;
}

.auth__left-container {
    background: url(https://images.unsplash.com/photo-1517044589904-826302db21fe?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1950&q=80);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.auth__login-form {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
}

.auth__login-form-control {
    margin-top: 15px;
}

.auth__login-button {
    text-align: center;
    margin-top: 20px;
    font-weight: bold;
}

.btn-info {
    font-weight: bold;
}