.item-page { 
    padding: 5px 8px;
    border: 1px solid #dee2e6;
}

.link-page {
    color: #5bc0de;
}

.link-page:hover {
    text-decoration: none;
    color: #5bc0ff
}

.item-active-page {
    background-color: #5bc0de;
}

.link-active-page {
    color: white;
}

.pagination__container {
    text-align: center;
}

li {
    display: inline;
}

.pagination-overriden {
    margin-top: 35px;
    font-size: 1rem;
    padding-left: 0;
    list-style: none;
    border-radius: 0.25rem;
    display: inline-table;
}